<script>
import Clipboard from 'clipboard';
import { mymixin } from '../views/mixin/mixin'
export default {
    mixins:[mymixin],
    data(){
        return {
            copyText: 'IT程序员老王',
        }
    },
    methods: {
        copyBtn(){
            let _this = this;
            let clipboard = new Clipboard(".copy-btn");
            clipboard.on("success", e => {
                _this.$message.success("公众号名称复制成功，微信搜索即可！")
                clipboard.destroy();
            });
            clipboard.on("error", e => {
                _this.$message.error("该浏览器不支持自动复制")
                clipboard.destroy();
            });
        },
        hClick(id){
            this.$emit('jupm', id)
        },
    },
}
</script>

