<template>
  <div class="header">
    <div class="logo">
      <img src="../assets/logo.jpg" alt="" />
      <div class="menu">
        <span
          v-for="(item, inx) in headMenuList"
          :key="inx"
          :class="inx == navIndex  ? 'active' : ''"
          @click="clickNav(item,inx)"
          >{{ item.label }}
        </span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      navIndex : 0,
      headMenuList: [
        {
          label: "前端资料",
          id: 0,
          path: '/',
        },
        {
          label: "实战笔记",
          id: 1,
          path: '/acnote',
        },
        {
          label: "面试题",
          id: 2,
          path: '/acnote',
        },
        {
          label: "实习上班自学交流区",
          id: 3,
          path: '',
        },
        // {
        //     label: "文字转语音",
        //     id: 1,
        // },
      ],
    };
  },
  watch: {
    $route: {
      handler(newV, oldV) {
        let path = this.$route.path;
        this.checkRouterLocal(path);
      },
    },
  },
  created(){
      if(this.$route.name == 'home'){
          this.navIndex = 0
      } else if(this.$route.name == 'acnote'){
          this.navIndex = 1
      }
  },
  methods: {
    clickNav(item,inx) {
      this.navIndex = inx
    //   this.$router.push(item.path)
      if(item.id == 3) {
        window.open('https://www.bilibili.com/video/BV1yZ4y1z7yc?spm_id_from=333.999.0.0');
      } else if(item.id == 1){
          window.open('http://mp.weixin.qq.com/mp/homepage?__biz=MzIxOTYzMjcxNw==&hid=18&sn=09f967eb92a2a63876e7d74c0aec6f0f&scene=18#wechat_redirect')
      } else if(item.id == 2){
          window.open('https://blog.csdn.net/qq_30351747/article/details/123605731')
      }
    },
    checkRouterLocal(path) {
        this.navIndex = this.headMenuList.findIndex(item => item.path === path);
    }
  },
};
</script>

