<script>
import MHeader from "../components/header.vue"
import LeftCom from "../components/leftcom.vue"
import { mymixin } from "./mixin/mixin"
export default {
  components: { MHeader, LeftCom },
  mixins:[mymixin],
  data(){
    return {

    }
  },
  methods: {
    hJump(domId){
      // 当前窗口正中心位置到指定dom位置的距离
            //页面滚动了的距离
            let height = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;        
           
           //指定dom到页面顶端的距离
            let dom = document.getElementById(domId);
            let domHeight = dom.offsetTop+60;
            
            //滚动距离计算
            var S = Number(height) - Number(domHeight) + 60;

            let d2 = document.getElementById('mains')
            //判断上滚还是下滚
            if(S<0){
                //下滚
                S = Math.abs(S);
                d2.scrollTop = S
            }else if(S==0){
                //不滚
                d2.scrollTop = 0
            }else{
                //上滚
                S = -S
                d2.scrollTop = S
            }
    },
  },
}
</script>
